.stepwizard-step p {
	margin-top: 10px;
}

.stepwizard-row {
	display: table-row;
}

.stepwizard {
	display: table;
	width: 100%;
	position: relative;
}

.stepwizard-step button[disabled] {
	opacity: 1 !important;
	filter: alpha(opacity=100) !important;
}

.stepwizard-row:before {
	top: 14px;
	bottom: 0;
	position: absolute;
	content: " ";
	width: 100%;
	height: 1px;
	background-color: #ccc;
	z-order: 0;
}

.stepwizard-step {
	display: table-cell;
	text-align: center;
	position: relative;
}

.btn-circle {
	width: 30px;
	height: 30px;
	text-align: center;
	padding: 6px 0;
	font-size: 12px;
	line-height: 1.428571429;
	border-radius: 15px;
}

.stepwizard-step .btn-default {
	color: #2780e3 !important;
	border-color: #2780e3;
	background: white;
	opacity: 1;
}

.stepwizard-step .btn-primary {
	color: white !important;
	background: #2780e3 !important;
	border-color: #2780e3;
}

.form-control:focus {
	border-color: #2780e3;
	-webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px #2780e3;
	box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px #2780e3;
}

.hide {
	display: none;
}

.btn-primary {
	color: #fff;
	background-color: #2780e3;
	border-color: #2780e3;
}

.btn-primary:hover {
	color: #fff;
	background-color: #15569f;
	border-color: #15569f;
}

#zbs_other_details {
	display: none;
}

.setup-content textarea {
	width: 90%;
}

.setup-content select {
	width: 90%;
	height: 30px !important;
	line-height: 30px !important;
}

input[type=checkbox],
input[type=radio] {
	width: 20px;
}

.clear {
	clear: both;
}

.pos-rel {
	position: relative;
}

#crm-name {
	position: absolute;
	top: 7px;
	left: 30px;
	color: white;
}

#zbs_other_details {
	height: 150px;
	margin-bottom: 20px;
}


