#switchBox {
	width: 140px;
	text-align: center;
	display: inline-block;
	margin-left: 120px;
	margin-right: 120px;
}

#switchBox .switchBoxLabel {
	font-size: 16px;
	color: #2780e3;
}

.switchCheckbox {
	width: 80px !important;
	height: 26px !important;
	background: #333 !important;
	margin: 20px auto !important;
	position: relative !important;
	border-radius: 50px !important;
	box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.5), 0px 1px 0px rgba(255, 255, 255, 0.2) !important;
}

.switchCheckbox:after {
	content: 'OFF' !important;
	color: #000 !important;
	position: absolute !important;
	right: 10px !important;
	z-index: 0 !important;
	font: 12px/26px Arial, sans-serif !important;
	font-weight: bold !important;
	text-shadow: 1px 1px 0px rgba(255, 255, 255, 0.15) !important;
}

.switchCheckbox:before {
	content: 'ON';
	color: #27ae60 !important;
	position: absolute !important;
	left: 10px !important;
	z-index: 0 !important;
	font: 12px/26px Arial, sans-serif !important;
	font-weight: bold !important;
}

.switchCheckbox label {
	margin-top: 0 !important;
	display: block !important;
	width: 34px !important;
	height: 20px !important;
	cursor: pointer !important;
	position: absolute !important;
	top: 3px !important;
	left: 3px !important;
	z-index: 1 !important;
	background: #fcfff4 !important;
	background: linear-gradient(to bottom, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%) !important;
	border-radius: 50px !important;
	transition: all 0.4s ease !important;
	box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.3) !important;
}

.switchCheckbox input[type=checkbox] {
	visibility: hidden !important;
}

.switchCheckbox input[type=checkbox]:checked+label {
	left: 43px !important;
}


